import React, { useState, useEffect } from "react";
import { Container, Col, Row, Button, Image } from "react-bootstrap";

const PicCol = (props) => {
  return (
    <>
      <Col className="col-12 col-md-6 ">
        <Image
          responsive="true"
          src={props.imgLink}
          alt={props.id}
          className=" rounded-lg shadow d-block mx-auto my-3"
          style={{ width: "inherit" }}
          id="projectImg"
        />
      </Col>
    </>
  );
};
const DataCol = (props) => {
  return (
    <>
      <Col className="align-self-center">
        <Container>
          <Row className="d-flex justify-content-center text-center">
            <p className="p-0 m-0" id="projectHeader">
              {props.header}
            </p>
          </Row>

          <Row>
            <p
              className="p-0 m-0 font-italic text-justify"
              id="projectDescription"
            >
              {props.description}
            </p>
          </Row>
          <Row className="d-flex justify-content-around p-3">
            <Button href={props.githubLink} className="btn btn-info ">
              Github
            </Button>
            <Button href={props.liveLink} className="btn btn-success ">
              View Live
            </Button>
          </Row>
        </Container>
      </Col>
    </>
  );
};

export const Project = ({
  id,
  imgLink,
  header,
  description,
  githubLink,
  liveLink,
}) => {
  const [size, setSize] = useState();

  const checkSize = () => {
    setSize(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener("resize", checkSize);
    return () => {
      window.removeEventListener("resize", checkSize);
    };
  }, [size]);
  return (
    <>
      {window.innerWidth > 768 ? (
        <>
          {console.log("above medium")}
          {id % 2 === 0 ? (
            <Row className="m-3 ">
              <PicCol id={id} imgLink={imgLink} />
              <DataCol
                header={header}
                description={description}
                githubLink={githubLink}
                liveLink={liveLink}
              />
            </Row>
          ) : (
            <Row className="m-3 ">
              <DataCol
                header={header}
                description={description}
                githubLink={githubLink}
                liveLink={liveLink}
              />
              <PicCol id={id} imgLink={imgLink} />
            </Row>
          )}
        </>
      ) : (
        <>
          {console.log("smaller than medium")}
          <Row className="border my-5">
            <PicCol id={id} imgLink={imgLink} />
            <DataCol
              header={header}
              description={description}
              githubLink={githubLink}
              liveLink={liveLink}
            />
          </Row>
        </>
      )}
    </>
  );
};

export default Project;
