import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { skillsetData } from "./data/skillsetData";
import Skill from "./Skill";

function Skillset() {
  return (
    <section id="skillset" className=" bg-light text-secondary py-3 ">
      <Container className="text-center font-italic">
        <Row>
          <Col>
            <h1>Skillset</h1>
          </Col>
        </Row>
        <Container className="flex-wrap align-items-center mx-auto py-5">
          {skillsetData.map((skill) => {
            return <Skill key={skill.id} {...skill} />;
          })}
        </Container>
        <div data-tilt></div>
      </Container>
    </section>
  );
}

export default Skillset;
