import React, { useEffect, useState, useRef } from "react";
import Tilty from "react-tilty";

const Skill = ({ header, img }) => {
  const [isHover, setIsHover] = useState(false);

  const el = useRef(null);

  useEffect(() => {
    //check if el is not null (after load and when on hover)
    //change the opacity on the inner-element class to 1 if hovered on particular element

    if (el) {
      if (isHover) {
        //console.log(el);
        el.current.style.opacity = "1";
      } else el.current.style.opacity = "0";
    }

    return () => {
      //cleanup
    };
  });

  return (
    <>
      <Tilty
        className="tilty"
        scale={1.05}
        perspective={500}
        reset={true}
        max={45}
        style={{
          backgroundImage: "url(" + img + ")",
        }}
        onMouseEnter={() => setIsHover(!isHover)}
        onMouseLeave={() => setIsHover(!isHover)}
      >
        <div className="inner-element" ref={el}>
          <div>{header}</div>
        </div>
      </Tilty>
    </>
  );
};

export default Skill;
