import React from "react";
import { Container, Row, Col, Image } from "react-bootstrap";

function About() {
  const social = [
    {
      id: 1,
      imgLink:
        "https://www.broadwayinchicago.com/wp-content/uploads/2020/11/facebook-logo-png-transparent-background.png",
      link: "#facebook",
    },
    {
      id: 2,
      imgLink: "http://assets.stickpng.com/images/58e91afdeb97430e81906504.png",
      link: "#linkedIn",
    },
    {
      id: 3,
      imgLink: "http://cdn.onlinewebfonts.com/svg/img_250252.png",
      link: "#github",
    },
  ];
  return (
    <section id="about" className=" bg-light text-secondary py-3 ">
      <Container className="text-center font-italic">
        <Row>
          <Col>
            <h1>About</h1>
          </Col>
        </Row>
        <Container className="shadow-lg rounded my-5 p-5 d-flex flex-wrap">
          <Container className=" text-dark  col-12 col-lg-6">
            <h2>Being my own R&amp;D</h2>
            <p className=" my-2 px-5">
              I am a self-taught web and software developer. I love it when I
              create something out of nothing, and that something is useful for
              those who need it.
            </p>
          </Container>
          <Container className=" text-dark   col-12 col-lg-6">
            <h2>My Drive</h2>
            <p className=" my-2 px-5">
              I build projects for fun(ctionality!) and self-development. I'm a
              fast learner and I am able to tackle new tasks with great
              positivity and persistence (towards perfection).
            </p>
          </Container>
          <Container fluid className="col-12 my-3">
            {social.map((item) => {
              return (
                <Image
                  key={item.id}
                  src={item.imgLink}
                  style={{ width: "40px", cursor: "pointer" }}
                  className="m-2 rounded"
                />
              );
            })}
          </Container>
        </Container>
      </Container>
    </section>
  );
}

export default About;
