import React from "react";
import { Navbar, Nav, Image } from "react-bootstrap";

import { Link, animateScroll as scroll } from "react-scroll";

function Navigation() {
  const hideNav = () => {
    var screenSize = window.innerWidth;
    if (screenSize < 992) {
      //mobile view -> hide navDropDown
      console.log("clicked for hiding");
    }
  };
  const scrollToTop = () => {
    scroll.scrollToTop();
  };
  const offset = -65;
  const dur = 1000;
  const smo = "easeOutExpo";
  return (
    <>
      <Navbar
        collapseOnSelect
        className="shadow"
        variant="dark"
        bg="dark"
        expand="lg"
        sticky="top"
        id="navigationBar"
        style={{ fontSize: "20px" }}
      >
        <Navbar.Brand onClick={scrollToTop}>
          <Image
            id="logoImg"
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1280px-React-icon.svg.png"
            rounded
            fluid
          ></Image>
        </Navbar.Brand>
        <Navbar.Toggle
          aria-controls="responsive-navbar-nav"
          data-toggle="dropdown"
          //NAVBAR DOESNT CLOSE AFTER BUTTON CLICK DUE TO STUPID <Link /> COMPONENT
          //id="togg"
        />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto">
            <Link
              className="nav-link"
              activeClass="active"
              to="projects"
              spy={true}
              smooth={smo}
              offset={offset}
              duration={dur}
              eventkey="1"
              onClick={hideNav}
            >
              Projects
            </Link>

            <Link
              className="nav-link"
              activeClass="active"
              to="skillset"
              spy={true}
              smooth={smo}
              offset={offset}
              duration={dur}
              eventkey="2"
              onClick={hideNav}
            >
              Skillset
            </Link>
            <Link
              className="nav-link"
              activeClass="active"
              to="about"
              spy={true}
              smooth={smo}
              offset={offset}
              duration={dur}
              eventkey="3"
              onClick={hideNav}
            >
              About
            </Link>
            <Link
              className="nav-link"
              activeClass="active"
              to="contact"
              spy={true}
              smooth={smo}
              offset={offset}
              duration={dur}
              eventkey="4"
              onClick={hideNav}
            >
              Contact
            </Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
}

export default Navigation;
