import React, { useState, useRef } from "react";

import { Container, Row, Col, Form, Button, Modal } from "react-bootstrap";
import { init } from "emailjs-com";

import emailjs from "emailjs-com";

function Contact() {
  const isEmail = require("sane-email-validation");
  var fade = require("fade");
  var el = document.querySelector("#invMsg");
  init("user_z3vb45f04nlIyF48p7OzJ"); //for Email sending
  const [showModal, setShowModal] = useState(false);
  const [data, setData] = useState({ name: "", email: "", message: "" });
  const [isNotValid, setIsNotValid] = useState(false);

  const handleType = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setData({ ...data, [name]: value });
  };

  const handleShow = (e) => {
    e.preventDefault();
    setShowModal(true);
  };
  const handleClose = (e) => {
    e.preventDefault();
    setShowModal(false);
  };

  const handleSubmit = (e) => {
    if (data.name && data.email && data.message) {
      //check for empty fields
      console.log("No empty fields");
      //check if data.email is valid email address
      if (isEmail(data.email)) {
        //it is - send email
        setIsNotValid(false);
        performSubmit(e);
        setShowModal(true);
        setData({ name: "", email: "", message: "" });
      } else {
        //it is not valid email - display error message
        console.log("wrong email");
        //setIsNotValid(true);
        fade.in(el, 500);
        const timer = setTimeout(() => {
          fade.out(el);
          //setIsNotValid(false);
        }, 2000);
        return () => {
          clearTimeout(timer);
        };
      }
    } else console.log("Has empty fields");
  };

  function performSubmit(e) {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_wxk3frh",
        "template_xggtre5",
        e.target,
        "user_z3vb45f04nlIyF48p7OzJ"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  }
  return (
    <section id="contact" className="bg-light text-secondary py-3 ">
      <Container>
        <Row>
          <Col>
            <h1 className="text-center font-italic">Contact</h1>
          </Col>
        </Row>

        <Container fluid className="d-flex flex-wrap ">
          <Container
            fluid
            className="mx-auto text-center text-light bg-dark pt-5"
          >
            <h4 className=" font-italic">Always feels good to keep in touch</h4>
          </Container>
          <Container fluid className="col-12 col-lg-6 bg-dark py-5 px-5 ">
            <Form onSubmit={handleSubmit}>
              <Row>
                <Col className="col-12 col-lg-6 my-2">
                  <Form.Control
                    type="input"
                    name="name"
                    value={data.name}
                    placeholder="Full Name"
                    onChange={handleType}
                  />
                </Col>
                <Col className="col-12 col-lg-6 my-2">
                  <Form.Control
                    type="input"
                    name="email"
                    value={data.email}
                    placeholder="Email Address"
                    onChange={handleType}
                  />
                </Col>
                <Col className="col-12 my-2">
                  <Form.Control
                    type="input"
                    name="message"
                    value={data.message}
                    as="textarea"
                    rows={3}
                    placeholder="Message for me"
                    onChange={handleType}
                  />
                </Col>
                <Col className="col-12 d-flex">
                  <Button
                    className="btn btn-success"
                    type="submit"
                    onClick={() => handleShow}
                  >
                    Send
                  </Button>
                  <div id="invMsg" className="m-auto">
                    Invalid email address
                  </div>
                </Col>
              </Row>
            </Form>
          </Container>
          <Container
            fluid
            className="col-12 col-md-6 bg-dark text-light d-flex"
          >
            <Container className="m-auto">
              <h1>
                get social icons from "about" (make them reusable component)
              </h1>
            </Container>
          </Container>
        </Container>
      </Container>
      <Modal show={showModal} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Email sent</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Thank you for the email, I will get back to you as soon as possible.
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleClose}>
            Thanks
          </Button>
        </Modal.Footer>
      </Modal>
    </section>
  );
}

export default Contact;
